<template>
  <v-row :justify="`center`">
   <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
     <template v-slot:activator="{ on, attrs }">
       <v-btn
         text
         v-bind="attrs"
         v-on="on"
       >
         USER INFO.
       </v-btn>
     </template>
     <v-card>
       <v-toolbar dark color="primary">
         <v-btn icon dark @click="dialog = false">
           <v-icon>mdi-close</v-icon>
         </v-btn>
         <v-toolbar-title>User Info. Setting</v-toolbar-title>
         <v-spacer></v-spacer>
         <v-toolbar-items>
           <v-btn dark text @click="dialog = false">Save</v-btn>
         </v-toolbar-items>
       </v-toolbar>
       <v-form>
        <v-container>
          <v-row>
            <v-col cols="12" sm="5">
              <v-text-field
                label="ID"
                :value="login_key"
                disabled
              ></v-text-field>
              <v-text-field
                label="Password"
                :value="login_value"
                :type="'password'"
                append-icon="mdi-pencil"
                disabled
                @click:append=""
              ></v-text-field>
              <v-text-field
                label="name"
                :value="name"
                disabled
                append-icon="mdi-pencil"
              ></v-text-field>
              <v-text-field
                label="Corporation"
                :value="corporation_name"
                disabled
                append-icon="mdi-pencil"
              ></v-text-field>
              <v-text-field
                label=" "
                :value="getIsActive"
                disabled
              ></v-text-field>
              <v-text-field
                label=" "
                :value="getQualifiedType"
                disabled
              ></v-text-field>
              <v-text-field
                label=" "
                :value="getUserType"
                disabled
              ></v-text-field>
              <v-text-field
                label="E-mail"
                :value="email"
                disabled
              ></v-text-field>
              <v-text-field
                label="Phone number"
                :value="mdn"
                append-icon="mdi-pencil"
                disabled
              ></v-text-field>
              <v-text-field
                label="Address"
                :value="address"
                disabled
              ></v-text-field>
            </v-col>
            <v-divider
               class="mx-4"
               vertical
            ></v-divider>
            <v-col cols="12" sm="6">
              <v-container>
                <v-row justify="center">
                  <v-col cols="12" sm="6">
                    <v-card flat class="mx-auto">
                    <v-img
                      :src="profile_image"
                      :style="`border: 1px solid #000000`"
                      contain
                    ></v-img>
                    </v-card>
                  </v-col>
                  <v-col
                    cols="12" sm="6"
                    class="text-center pl-0"
                  >
                    <v-row
                      class="flex-column ma-0 fill-height"
                      justify="center"
                      align="center"
                    >
                      <div class="pr-4">
                        <v-file-input
                          v-model="image_pf"
                          accept="image/png, image/jpeg, image/bmp"
                          :placeholder="getPfImagePlaceholder"
                          :label="` `"
                          prepend-icon="mdi-paperclip"
                          type="file"
                        >
                          <template v-slot:selection="{ text }">
                            <v-chip
                              small
                              label
                              color="primary"
                            >
                              {{ text }}
                            </v-chip>
                          </template>
                        </v-file-input>
                      </div>
                    </v-row>
                  </v-col>
                </v-row>
              </v-container>
              <v-container>
                <v-row justify="center">
                  <v-col cols="12" sm="6">
                    <v-card flat class="mx-auto">
                    <v-img
                      :src="business_card"
                      :style="`border: 1px solid #000000`"
                      contain
                    ></v-img>
                    </v-card>
                  </v-col>
                  <v-col
                    cols="12" sm="6"
                    class="text-center pl-0"
                  >
                    <v-row
                      class="flex-column ma-0 fill-height"
                      justify="center"
                      align="center"
                    >
                      <div class="pr-4">
                        <v-file-input
                          v-model="image_bc"
                          accept="image/png, image/jpeg, image/bmp"
                          :placeholder="getBCImagePlaceholder"
                          :label="` `"
                          prepend-icon="mdi-paperclip"
                          type="file"
                        >
                          <template v-slot:selection="{ text }">
                            <v-chip
                              small
                              label
                              color="primary"
                            >
                              {{ text }}
                            </v-chip>
                          </template>
                        </v-file-input>
                      </div>
                    </v-row>
                  </v-col>
                </v-row>
              </v-container>
            </v-col>
          </v-row>
        </v-container>
      </v-form>
     </v-card>
   </v-dialog>
 </v-row>
</template>

<script>
  import {mapGetters, mapMutations, mapActions} from 'vuex'

  export default {
    name: 'BaseUserInfoDialog',

    inheritAttrs: false,

    props: {
      image_url: [Array, String],
      title: String,
      sub_title: String,
      description: String,
      order_forward: Boolean,
    },
    data() {
      return {
        dialog: false,
        login_key: '',
        login_value: '*******',
        name: '',
        corporation_name: '',
        is_active: false,
        qualified: false,
        user_type: 0,
        email: '',
        mdn: '',
        profile_image: '',
        address: '',
        business_card: '',
        image_pf: null,
        image_bc: null,
      }
    },
    created() {
      this.updateUserInfo(this.get_user_info)
    },
    computed: {
      ...mapGetters('user', [
        'get_user_info'
      ]),
      getBCImagePlaceholder() {
        if (this.business_card !== '') {
          return ' '
        } else {
          return ' '
        }
      },
      getPfImagePlaceholder() {
        if (this.profile_image !== '') {
          return ' '
        } else {
          return ' '
        }
      },
      getUserType() {
        if (this.user_type === 0) {
          return ''
        } else if (this.user_type === 1) {
          return 'Venture Capital'
        } else if (this.user_type === 2) {
          return 'Startup'
        } else {
          return ''
        }
      },
      getIsActive() {
        if (this.is_active) {
          return ' '
        } else {
          return ''
        }
      },
      getQualifiedType() {
        if (this.qualified) {
          return ' '
        } else {
          return ''
        }
      },
    },
    methods: {
      updateUserInfo(obj) {
        this.login_key = obj.login_key
        this.name = obj.name
        this.corporation_name = obj.corporation_name
        this.is_active = obj.is_active
        this.qualified = obj.qualified
        this.user_type = obj.user_type
        this.email = obj.email
        this.mdn = obj.mdn
        this.profile_image = obj.profile_image
        this.address = obj.address
        this.business_card = obj.business_card
      }
    }
  }
</script>
