<template>
  <v-row :justify="`center`">
  <v-dialog v-model="dialog" width="300" transition="dialog-bottom-transition">
    <template v-slot:activator="{ on, attrs }">
      <v-btn
        text
        v-bind="attrs"
        v-on="on"
      >
        LOG OUT
      </v-btn>
    </template>
    <v-card>
        <v-card-title class="headline primary white--text">
           
        </v-card-title>

        <v-card-text class="pt-4">
               . ?
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="signOut"
          >
            
          </v-btn>
          <v-btn
            color="grey"
            text
            @click="dialog = false"
          >
            
          </v-btn>
        </v-card-actions>
      </v-card>
 </v-dialog>
 </v-row>
</template>

<script>
  import {mapGetters, mapMutations, mapActions} from 'vuex'

  export default {
    name: 'BaseSignOutBtn',
    data() {
      return {
        dialog: false
      }
    },
    methods: {
      ...mapActions('user', [
        'signout'
      ]),
      signOut() {
        let payload = {
          cb_res : (result) => {
            this.dialog = false
          },
          cb_error : (error) => {
            alert('.     .')
          }
        }
        this.$store.dispatch('user/signout', payload)
      }
    }
  }
</script>
